<template>
	<div class="list">
	<h1>ToDoList</h1>
	<input class="inputBox" type="text" v-model.trim="tempTxt" @keyup.enter="addList" placeholder="請輸入待辦事項">
	<div class="btn" @click="addList">確認</div>
	<ul class="listBox">
		<li v-for="(item,index) in list" :key="index">
			<!-- <input type="checkbox" :id="item.id"> -->
			<label :class="{completed:item.completed}" @click="listCompleted(item.completed,item.id)">
				{{item.text}}
			</label>
		</li>
	</ul>
	</div>
</template>

<script>
export default {
	data () {
		return {
			tempTxt: '',
		}
	},
	computed:{
		list(){
			return this.$store.state.list;
		}
	},
	methods:{
		addList: function(e){
			let date = new Date();
			let text = this.tempTxt;
			if(!text) return false;
			this.tempTxt = "";
			this.$store.dispatch('addList',{
				id: date.getTime(),
				text: text,
				completed: false 
			})
		},
		listCompleted:function(isCompleted,itemId){
			this.$store.dispatch('isCompleted',{isCompleted,itemId});
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
	*
		// outline: 1px solid #f00

	.inputBox
		font-size: 16px
		padding: 2px

	.btn
		font-size: 16px
		display: inline-block
		border: 1px solid #ccc
		padding: 1px 2px
		cursor: pointer
		color: #666
		transition: .3s
		&:hover
			background-color: #eee
			color: #333

	.listBox
		width: 20%
		margin: 10px auto
		padding: 0
		border: 1px solid #999
		li
			list-style: none
			padding: 5px 10px
			position: relative
			&:nth-child(odd)
				background-color: #eee
			input
				position: absolute
				left: 0
				top: 50%
				transform: translateY(-50%)
			label
				width: 90%
				display: block
				margin: auto
				

	.completed
		text-decoration: line-through
	
</style>



